untry which were probably put largely into consideration when deciding whether or not they would immigrate to America. ")])])])]),_c('section',{staticClass:"home__main-question-container home__main-question-container--main"},[_c('div',{staticClass:"home__main-question-text home__main-question-text--main"},[_c('div',{staticClass:"home__main-question home__main-question--main"},[_c('h3',{staticClass:"home__question-q home__question-q--main"},[_vm._v("MAIN QUESTION: What does it mean to be an American?")])]),_c('div',{staticClass:"home__main-response home__main-response--main"},[_c('div',{staticClass:"home__question-r home__question-r--main"},[_c('p',{staticClass:"home__question-r--thesis"},[_vm._v("To classify yourself as an American is to consider yourself as one and to contribute to American society.")]),_c('p',{staticClass:"home__question-r--evidence"},[_vm._v("Nikola Tesla is an extreme case as an American. According to his biography, he immigrated to New York City in 1884 so that he could get hired by Thomas Edison. This establishes his identity as an American because he is willing to contribute to science and work with a famous inventor to greatly impact society. Moreover, in 1891, he received his American citizenship. This is what I meant by \"considering yourself as one\" because he went out of his way to receive an American citizenship many years after immigrating there. I also believe that he was classfied as an American before getting his citizenship. Finally, his inventions such as the AC electrical system and the Tesla coil contribute to today's society in America, definitely classifying him as an American. ")]),_c('p',{staticClass:"home__question-r--evidence"},[_vm._v(" In the case of Kim Wong, he said that he doesn't want to vote if it doesn't matter to him because it would change the vote. I believe this is a good representation in being an American because to be an American is to contribute to American society. In this case, he is contributing to society by only voting when he actually has an opinion during the presidential election. I believe that a person shouldn't vote if the current presidential election isn't important to them. Also, during the interview he mentioned something about having a Facebook account and occassionally watching the news. I believe that involving yourself in American culture such as American media is a good representation at being an American and contributes to society because it supports these companies economically. Finally, I believe that a big part of classifying yourself as an American is to learn English. Kim Wong is a good example of this because he actually went to college for English. Many nationalaties have their own distinct language and I believe that speaking English is a big part of classifying yourself as an American. ")]),_c('p',{staticClass:"home__question-r--evidence"},[_vm._v(" Contributing to society means following the rules and laws. In my case, I believed people should abide by and acknowledge the Constitution. Abiding by and acknowledging the Constitution contributes to society because it involuntarily leads to less unrest and disagreements. Also, if just, one should follow the very foundation of the country they live in. Democracy symbolizes America and is a huge pull factor for immigrants. To accept oneself as an American means to follow a government type that represents America. Racial and Gender Equality are very important attributes of America which make America distinct. I believe these two traits are a huge part of America and any American should abide by these traits in order to accept themselves as an American. ")])])])])])])])])}]

